import Application from './af/core/prototypes/Application';
import Canvas from './af/modules/Canvas';
import Box from './af/modules/Canvas/Prototypes/Box';
import Layer from './af/modules/Canvas/lib/Layer';
import TextBlock from './af/modules/Canvas/Prototypes/TextBlock';
import RenderEngine from './af/modules/Canvas/lib/RenderEngine';
import { default as Assets, BLOCK_SIZE, BLOCK_UPPER_SPACE, BLOCK_HEIGHT } from './assets';
import Map from './map';

const CHAR_PADDING = -35;

const App = {

    canvas: null,

    init([GrassBlock, DirtBlock, ShadowEast, ShadowWest, ShadowNorth, ShadowSouth, CharBoy]) {
        this.canvas = Object.create(Canvas).constructor('#canvas');

        const randomGuy = Object.create(CharBoy);
        const currentMapLevel = 1;
        const heightLevel = (currentMapLevel * BLOCK_HEIGHT);

        randomGuy.offsetY = CHAR_PADDING;
        randomGuy.setProperty('y', heightLevel);

        const walkableMap = Object.create(Layer).constructor({
            x: 0,
            y: 0,
            height: BLOCK_UPPER_SPACE + (BLOCK_SIZE * 4),
            width: GrassBlock.width * 4,
        });

        walkableMap.addChild(randomGuy);

        const layer2 = Object.create(Layer).constructor({
            x: 50,
            y: 50,
            height: 50,
            width: 150,
        });

        const someText = Object.create(TextBlock).constructor({
            height: 50,
            width: 150,
            text: ''
        });

        layer2.addChild(someText);

        this.canvas.addChild(Object.create(Box).constructor({ width: this.canvas.width, height: this.canvas.height }));
        this.buildMap(Map, DirtBlock, ShadowWest, GrassBlock, ShadowEast, ShadowNorth, ShadowSouth);

        walkableMap.addChild(layer2);
        this.canvas.addChild(walkableMap);
        this.canvas.startRendering();

        setInterval(() => {
            const heightLevel = (currentMapLevel * BLOCK_HEIGHT);

            if (randomGuy.x === GrassBlock.width * 3 && randomGuy.y < heightLevel + BLOCK_SIZE) {
                randomGuy.setProperty('y', randomGuy.y + 1);
            } else if (randomGuy.x > 0 && randomGuy.y === heightLevel + BLOCK_SIZE){
                randomGuy.setProperty('x', randomGuy.x - 1);
            } else if (randomGuy.x === 0 && randomGuy.y > heightLevel) {
                randomGuy.setProperty('y', randomGuy.y - 1);
            } else {
                randomGuy.setProperty('x', randomGuy.x + 1);
            }

            const fps = Math.round(RenderEngine.performance.fps);
            const frames = Math.round(RenderEngine.performance.renderedFrames);
            const duration = Math.round(RenderEngine.performance.lastFrameDuration);

            someText.setProperty('text', `FPS: ${fps}\nRenderedFrames: ${frames}\nFrameDuration: ${duration}ms`);
        }, 10);
    },

    buildMap(map, DirtBlock, ShadowWest, GrassBlock, ShadowEast, ShadowNorth, ShadowSouth) {
        let { size: mapSize } = map;
        map = map.data;

        const mapContainer = Object.create(Layer).constructor({
            height: BLOCK_UPPER_SPACE + (BLOCK_SIZE * mapSize[0]) + BLOCK_HEIGHT,
            width: GrassBlock.width * mapSize[1],
        });

        const mapping = {
            'grass': GrassBlock,
            'dirt': DirtBlock,
        };

        for (let i = map.length-1; i > -1; i--) {
            const layer = map[i];
            const container = Object.create(Layer).constructor({
                y: BLOCK_HEIGHT * i,
                height: BLOCK_UPPER_SPACE + (BLOCK_SIZE * layer.length) + BLOCK_HEIGHT,
                width: GrassBlock.width * Math.max(...layer.map(item => item.length)),
            });

            for (let r = 0; r < layer.length; r++) {
                const blockRow = layer[r];

                for (let b = 0; b < blockRow.length; b++) {
                    const blockType = blockRow[b];

                    if (blockType) {
                        const block = Object.create(mapping[blockType]);

                        block.setProperty('x', GrassBlock.width * b);
                        block.setProperty('y', BLOCK_SIZE * r);

                        if (map[i+1] && map[i+1][r-1] && map[i+1][r-1][b] && !map[i][r-1][b]) {
                            const shadow = Object.create(ShadowSouth);

                            shadow.setProperty('y', BLOCK_SIZE * (r-1) + BLOCK_HEIGHT);
                            shadow.setProperty('x', GrassBlock.width * b);
                            container.addChild(shadow);
                        }

                        container.addChild(block);

                        // if there is a block one layer lower and one to the left, but
                        // no block on the same layer and one to the left
                        if (map[i+1] && map[i+1][r][b-1] && !map[i][r][b-1]) {
                            const shadow = Object.create(ShadowEast);

                            shadow.setProperty('y', (BLOCK_SIZE * r) + BLOCK_HEIGHT);
                            shadow.setProperty('x', GrassBlock.width * (b-1));

                            container.addChild(shadow);
                        }

                        if (map[i+1] && map[i+1][r][b+1] && !map[i][r][b+1]) {
                            const shadow = Object.create(ShadowWest);

                            shadow.setProperty('y', (BLOCK_SIZE * r) + BLOCK_HEIGHT);
                            shadow.setProperty('x', GrassBlock.width * (b+1));
                            container.addChild(shadow);
                        }

                        if (map[i+1] && map[i+1][r+1] && map[i+1][r+1][b] && !map[i][r+1][b]) {
                            const shadow = Object.create(ShadowNorth);

                            shadow.setProperty('y', BLOCK_SIZE * (r+1) + BLOCK_HEIGHT);
                            shadow.setProperty('x', GrassBlock.width * b);
                            container.addChild(shadow);
                        }
                    }
                }
            }

            mapContainer.addChild(container);
        }

        this.canvas.addChild(mapContainer);
    },

    __proto__: Application,
};

Promise.all([
    Assets.blocks.grass,
    Assets.blocks.dirt,
    Assets.blocks.shadow.east,
    Assets.blocks.shadow.west,
    Assets.blocks.shadow.north,
    Assets.blocks.shadow.south,
    Assets.chars.boy,
]).then(App.init.bind(App));



